import React, { useMemo } from "react";
import { ResponsiveContainer, PieChart, Pie, Cell, Tooltip } from "recharts";

const COLORS = ["#a855f7", "#6366f1", "#10b981", "#f59e0b"];

const formatCurrency = (value) => {
  if (!value) return "0";
  return `${value.toLocaleString()}`;
};

const BestInvestmentPie = ({ data = [], areas = [] }) => {
  const pieData = useMemo(() => {
    if (!data.length || !areas.length) return [];
    const first = data[0] || {};
    const last = data[data.length - 1] || {};

    return areas.map((area, index) => {
      const priceKey = `${area} Price`;
      const unitsKey = `${area} Units`;
      const startPrice = first[priceKey] || 0;
      const endPrice = last[priceKey] || 0;
      const totalUnits = data.reduce((sum, row) => sum + (row[unitsKey] || 0), 0);
      const growth = startPrice > 0 ? (((endPrice - startPrice) / startPrice) * 100) : 0;

      return {
        name: area,
        value: Math.max(endPrice, 1),
        growth: Number(growth.toFixed(1)),
        endPrice,
        totalUnits,
        color: COLORS[index % COLORS.length]
      };
    });
  }, [data, areas]);

  if (!pieData.length) return null;

  return (
    <div style={{
      margin: "12px 0",
      padding: "18px 22px",
      borderRadius: "18px",
      background: "linear-gradient(145deg, #121939, #0b0f24)",
      border: "1px solid rgba(148, 163, 184, 0.15)",
      boxShadow: "0 12px 30px rgba(0,0,0,0.35)",
      textAlign: "left"
    }}>
      <h5 style={{ color: "#f5f3ff", marginBottom: "16px" }}>
         Investment Share (Final Price  Growth)
      </h5>
      <div style={{ display: "flex", justifyContent: "flex-start" }}>
        <div style={{ width: "100%", minWidth: 260 }}>
          <ResponsiveContainer width="100%" height={360}>
            <PieChart margin={{ left: 0 }}>
              <Pie
                data={pieData}
                dataKey="value"
                nameKey="name"
                innerRadius={0}
                outerRadius={150}
                paddingAngle={2}
                label={({ name, percent }) => `${name} ${(percent * 100).toFixed(0)}%`}
                labelLine={false}
              >
                {pieData.map((entry, index) => (
                  <Cell key={`cell-${entry.name}`} fill={entry.color} stroke="#0b0f24" strokeWidth={1} />
                ))}
              </Pie>
              <Tooltip
                formatter={(value, name, payload) => [
                  formatCurrency(payload.payload.endPrice),
                  `${name}  ${payload.payload.growth}% growth  ${payload.payload.totalUnits.toLocaleString()} units`
                ]}
                contentStyle={{
                  background: "#0f172a",
                  border: "1px solid rgba(148,163,184,0.2)",
                  borderRadius: "12px"
                }}
                labelStyle={{ color: "#cbd5f5", fontWeight: 600 }}
                itemStyle={{ color: "#f8fafc", fontWeight: 600 }}
              />
            </PieChart>
          </ResponsiveContainer>
        </div>
      </div>
    </div>
  );
};

export default BestInvestmentPie;

