import React, { useState, useRef, useEffect } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";
import { analyzeQuery } from "./api";
import PriceChart from "./PriceChart";
import BestInvestmentPie from "./BestInvestmentPie";
import DataTable from "./DataTable";

const areas = ["Wakad", "Aundh", "Akurdi", "Ambegaon Budruk"];

const detectAreaFromText = (text) => {
  const lower = text.toLowerCase();
  if (lower.includes("wakad")) return "Wakad";
  if (lower.includes("aundh")) return "Aundh";
  if (lower.includes("akurdi") || lower.includes("chinchwad")) return "Akurdi";
  if (lower.includes("ambegaon")) return "Ambegaon Budruk";
  return null;
};

function App() {
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState("");
  const [loading, setLoading] = useState(false);
  const messagesEndRef = useRef(null);

  useEffect(() => {
    if (messages.length === 0) {
      setMessages([{
        id: 1,
        role: "assistant",
        summary: "Hi! Welcome to **EstateInsight** \n\nI'm your Pune real estate expert.\n\nWhat would you like to know?",
        showOptions: true
      }]);
    }
  }, [messages.length]);

  const scrollToBottom = () => messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  useEffect(scrollToBottom, [messages]);

  const shouldShowMainOptions = messages[messages.length - 1]?.showOptions;

  const mainOptions = () => (
    <div style={{ marginTop: "28px" }}>
      <div className="options-grid">
        <button onClick={() => startAnalyze()} className="option-btn"> Analyze an area</button>
        <button onClick={() => startCompare()} className="option-btn"> Compare areas</button>
        <button onClick={() => bestInvestment()} className="option-btn"> Best investment area</button>
        <button onClick={() => endChat()} className="option-btn" style={{ background: "#dc2626", borderColor: "#ef4444" }}> End chat</button>
      </div>
      <p style={{ color: "#64748b", fontSize: "0.9rem", marginTop: "20px", textAlign: "center", fontStyle: "italic" }}>
        What else can I help you with?
      </p>
    </div>
  );

  const startAnalyze = () => {
    setMessages(prev => [...prev, {
      id: Date.now(),
      role: "assistant",
      summary: "Which area would you like to analyze?",
      areaButtons: areas,
      mode: "analyze"
    }]);
  };

  const startCompare = () => {
    setMessages(prev => [...prev, {
      id: Date.now(),
      role: "assistant",
      summary: "Select two areas to compare (click to select):",
      areaButtons: areas,
      mode: "compare",
      selected: []
    }]);
  };

  const bestInvestment = () =>
    sendQuery("best investment area in Wakad, Aundh, Akurdi, Ambegaon Budruk");
  const endChat = () => {
    setMessages(prev => [...prev, {
      id: Date.now(),
      role: "assistant",
      summary: "Thank you for using **EstateInsight**! \n\nHave a great day and happy investing in Pune real estate!\n\nSee you again soon.",
    }]);
  };

  const handleAreaClick = (area) => {
    const last = messages[messages.length - 1];
    if (last.mode === "analyze") {
      sendQuery(`analyze ${area}`);
    } else if (last.mode === "compare") {
      let selected = last.selected || [];
      if (selected.includes(area)) {
        selected = selected.filter(a => a !== area);
      } else if (selected.length < 2) {
        selected.push(area);
      }
      if (selected.length === 2) {
        sendQuery(`compare ${selected[0]} and ${selected[1]}`);
      } else {
        setMessages(prev => {
          const newMessages = [...prev];
          newMessages[newMessages.length - 1] = { ...newMessages[newMessages.length - 1], selected };
          return newMessages;
        });
      }
    }
  };

  const sendQuery = async (text) => {
  if (loading || !text.trim()) return;

  const lower = text.toLowerCase();
  const detectedArea = detectAreaFromText(text);
  let finalQuery = text.trim();

  //  Detect best-investment style queries
  const isBestQuery =
    lower.includes("best") ||
    lower.includes("investment") ||
    lower.includes("top") ||
    lower.includes("highest") ||
    lower.includes("good investment");

  //  Any best-investment query (typed or button) is normalized
  // to a canonical multi-area question so the backend always
  // evaluates all supported areas together.
  if (isBestQuery) {
    finalQuery = "best investment area in Wakad, Aundh, Akurdi, Ambegaon Budruk";
  }

  //  Rewrite ONLY normal queries (not best-investment)
  if (detectedArea && !isBestQuery) {
    if (lower.includes("analyze") || lower.includes("analysis") || !lower.includes("compare")) {
      finalQuery = `analyze ${detectedArea}`;
    }
  }

  // Add user message
  setMessages(prev => [...prev, { id: Date.now(), role: "user", content: text }]);
  setInput("");
  setLoading(true);

  try {
    const res = await analyzeQuery(finalQuery);

    // If backend returned an error payload (e.g. server unreachable), surface it clearly
    if (res?.error || res?.detail) {
      setMessages(prev => [
        ...prev,
        {
          id: Date.now(),
          role: "assistant",
          summary: res.error || res.detail || "Sorry, something went wrong. Please try again.",
          showOptions: true
        }
      ]);
      return;
    }

    setMessages(prev => [
      ...prev,
      {
        id: Date.now(),
        role: "assistant",
        summary: res.summary || "No data found.",
        areas: res.areas || [],
        chart: res.chart || [],
        tables: res.tables || {},
        showOptions: true,
        bestMode: isBestQuery
      }
    ]);
  } catch (err) {
    setMessages(prev => [
      ...prev,
      {
        id: Date.now(),
        role: "assistant",
        summary: "Sorry, something went wrong. Please try again.",
        showOptions: true,
        bestMode: isBestQuery
      }
    ]);
  } finally {
    setLoading(false);
  }
};


  const downloadCSV = (area, data) => {
    const headers = Object.keys(data[0]);
    const csv = [headers.join(","), ...data.map(row => headers.map(h => `"${row[h] || ''}"`).join(","))].join("\n");
    const blob = new Blob([csv], { type: "text/csv" });
    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = `${area}_2020-2024.csv`;
    a.click();
  };

  return (
    <div className="app-container">
      <div className="chat-container">
        <div className="chat-header">
          <h1>EstateInsight</h1>
          <p>Your AI Real Estate Analyst</p>
        </div>

        <div className="chat-messages">
          {messages.map(msg => (
            <div key={msg.id} className={`message ${msg.role}`} style={{ marginBottom: "32px" }}>
              <div className="avatar">
                {msg.role === "user" ? "" : ""}
              </div>
              <div className="message-bubble">
                {msg.content && <p style={{ margin: "0 0 12px 0", fontWeight: "500" }}>{msg.content}</p>}
                {msg.summary && <div style={{ whiteSpace: "pre-line", lineHeight: "1.7", fontSize: "1.02rem" }}>{msg.summary}</div>}

                {msg.chart && msg.chart.length > 0 && (
                  <div style={{ margin: "28px 0" }}>
                    {msg.bestMode ? (
                      <BestInvestmentPie data={msg.chart} areas={msg.areas} />
                    ) : (
                      <PriceChart data={msg.chart} areas={msg.areas} />
                    )}
                  </div>
                )}

                {msg.tables && Object.entries(msg.tables).map(([area, table]) => (
                  <div key={area} className="result-card" style={{ margin: "28px 0" }}>
                    <div className="result-header">
                      <h6> {area}</h6>
                      <button onClick={() => downloadCSV(area, table)} className="download-btn">Download CSV</button>
                    </div>
                    <DataTable rows={table} />
                  </div>
                ))}

                {msg.areaButtons && (
                  <div className="options-grid" style={{ marginTop: "20px" }}>
                    {msg.areaButtons.map(a => (
                      <button
                        key={a}
                        onClick={() => handleAreaClick(a)}
                        className="option-btn"
                        style={{
                          background: msg.selected?.includes(a) ? "#8b5cf6" : "",
                          color: msg.selected?.includes(a) ? "white" : ""
                        }}
                      >
                        {a} {msg.selected?.includes(a) && ""}
                      </button>
                    ))}
                  </div>
                )}

              </div>
            </div>
          ))}

          {shouldShowMainOptions && (
            <div className="message assistant" style={{ marginBottom: "32px" }}>
              <div className="avatar"></div>
              <div className="message-bubble">
                {mainOptions()}
              </div>
            </div>
          )}

          {loading && (
            <div className="message assistant" style={{ marginBottom: "32px" }}>
              <div className="avatar"></div>
              <div className="message-bubble">
                <p> Analyzing data for you...</p>
              </div>
            </div>
          )}
          <div ref={messagesEndRef} />
        </div>

        <form className="chat-input-form" onSubmit={(e) => { e.preventDefault(); sendQuery(input); setInput(""); }}>
          <input
            value={input}
            onChange={(e) => setInput(e.target.value)}
            placeholder="Ask anything... (e.g., analyze ambegaon)"
            disabled={loading}
          />
          <button type="submit" disabled={loading || !input.trim()}>Send</button>
        </form>
      </div>
    </div>
  );
}

export default App;