import React from "react";

const DataTable = ({ rows }) => {
  if (!rows || rows.length === 0) return null;

  const columns = Object.keys(rows[0]);

  return (
    <div style={{ maxHeight: "400px", overflow: "auto", marginTop: "12px", border: "1px solid #334155", borderRadius: "8px" }}>
      <table style={{ width: "100%", borderCollapse: "collapse", fontSize: "0.9rem" }}>
        <thead>
          <tr style={{ background: "#1e293b", position: "sticky", top: 0 }}>
            {columns.map(col => (
              <th key={col} style={{ padding: "12px 8px", textAlign: "left", borderBottom: "1px solid #334155", color: "#e2e8f0" }}>
                {col}
              </th>
            ))}
          </tr>
        </thead>
        <tbody>
          {rows.map((row, i) => (
            <tr key={i} style={{ background: i % 2 === 0 ? "#0f172a" : "#1e293b" }}>
              {columns.map(col => (
                <td key={col} style={{ padding: "10px 8px", borderBottom: "1px solid #334155", color: "#cbd5e1" }}>
                  {row[col]}
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default DataTable;